@import '~animate.css/animate.css';
@import '~antd/dist/antd.min.css';
@import '~react-responsive-carousel/lib/styles/carousel.css';
/*  */
.react-rotate{
    -webkit-animation: rotate 3s linear infinite;
    animation: rotate 3s linear infinite;
}
 @-webkit-keyframes rotate{from{-webkit-transform: rotate(0deg)}
     to{-webkit-transform: rotate(360deg)}
 }
 @keyframes rotate{from{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
     to{-webkit-transform: rotate(359deg);transform: rotate(359deg)}
 }
#root{
  width: 100%;
  height: 100%;
}
#flexBox{
  padding: 20px;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  border-top: 24px solid #f2f2f2;
  border-bottom: 24px solid #f2f2f2;
  border-left: 16px solid #f2f2f2;
  border-right: 16px solid #f2f2f2;
  background-color: #fff;
  background:-webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,0)), to(rgba(255,255,255,1))), url("../imgs/bg.png");
  background:-webkit-linear-gradient(bottom, rgba(255,255,255,0), rgba(255,255,255,1)), url("../imgs/bg.png");
  background:-o-linear-gradient(bottom, rgba(255,255,255,0), rgba(255,255,255,1)), url("../imgs/bg.png");
  background:linear-gradient(to top, rgba(255,255,255,0), rgba(255,255,255,1)), url("../imgs/bg.png");
  background-size: cover;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  
}
/*  */
#contentScale{
  width: 1920px;
  height: 1080px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
#fullScreenElement{
  width: 100%;
  height: 100%;
  background:-webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,0)), to(rgba(255,255,255,1))), url("../imgs/bg.png");
  background:-webkit-linear-gradient(bottom, rgba(255,255,255,0), rgba(255,255,255,1)), url("../imgs/bg.png");
  background:-o-linear-gradient(bottom, rgba(255,255,255,0), rgba(255,255,255,1)), url("../imgs/bg.png");
  background:linear-gradient(to top, rgba(255,255,255,0), rgba(255,255,255,1)), url("../imgs/bg.png");
}
.pageContent{
  width: 100%;
  height: 100%;
}
.fontS20{
  font-size: 20px;
}
.paddingLR20{
  padding-left: 20px;
}
.logo{
  max-height: 40px;
}
.rt{
  float: right;
}
.lf{
  float: left;
}
.clear:after {
  clear:both;
  content:'.';
  display:block;
  width: 0;
  height: 0;
  visibility:hidden;
}
.content{
  width: 100%;
  height: 100%;
  position: relative;
}
.mapChina{
  width: 100%;
  height: 100%;
}
.count{
 font-size: 30px;
 font-weight: bold;
}
.ring{
  position: absolute;
  left:0;
  top:100px;
  width: 300px;
  height: 300px;
}
/*  */
.my-carousel{
  position: absolute;
  top:0;
  left:0;
  width:50%;
}
.anticon:hover{
  font-size: 24px;
  /* margin: 12px 0 16px; */
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}


